#body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#title {
  text-align: center;
  font-family: arial, sans-serif;
}

.tabulka_blok {
  text-align: center;
  font-family: arial, sans-serif;
}

#table {
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  font-size: 80%;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 100%;
  table-layout: auto;
}

#table td textarea {
 width: 100%;
 min-width: 30px;
 word-wrap: break-all;
 resize: none;
}

#table td, #table th {
  min-width: 30px;
  word-break: break-all;
  border: 1px solid #ddd;
  padding: 1%;

}

#input {
  min-width: 30px;
  word-break: break-all;
  border: 1px solid #ddd;
  padding: 1%;

}

#table td button {
     font-size: 80%;
     }

#table tr:nth-child(even){background-color: #f2f2f2;}

#table tr:hover {background-color: #ddd;}

#table th {
  padding-top: 1%;
  padding-bottom: 1%;
  text-align: center;
  background-color: #4CAF50;
  color: white;
  }

#form textarea {
 width: 100%;
 }